import * as React from 'react';
import classnames from 'classnames';

import * as s from './Section.module.css';

const Section:React.FunctionComponent<React.HTMLAttributes<HTMLElement>> = props => {
	return (
		<section id={props.id} className={classnames(s.root, props.className)}>
			{props.children}
		</section>
	);
};

export default Section;