import * as React from 'react';
import classnames from 'classnames';

import { TitleProps, LevelType, TagNameType } from './Title.types';

import * as s from './Title.module.css';

const Title: React.FunctionComponent<TitleProps> = (props) => {
	const level: LevelType = props.level && props.level > 0 && props.level <= 6 ? props.level : 1;
	const rootClassNames = classnames(s.root, props.className, {
		[s.level1]: level === 1,
		[s.level2]: level === 2,
		[s.level3]: level === 3,
		[s.level4]: level === 4,
		[s.level5]: level === 5,
		[s.level6]: level === 6
	});
	const TitleTagName: TagNameType = props.tagName
		? props.tagName as TagNameType
		: `h${level}` as TagNameType;

	return <TitleTagName id={props.id} className={rootClassNames}>{props.children}</TitleTagName>
};

export default Title;