import * as React from 'react';
import classnames from 'classnames';
import useBreakpoint from 'use-breakpoint';

import { breakpoints } from '../../tools/constants';

import Container from '../Container';
import Text from '../Text';
import Button from '../Button';

import PhoneIcon from '@iconscout/unicons/svg/line/phone.svg';
import EnvelopeIcon from '@iconscout/unicons/svg/line/envelope.svg';
import BarsIcon from '@iconscout/unicons/svg/line/bars.svg';
import CloseIcon from '@iconscout/unicons/svg/line/times.svg';
import WhatsAppIcon from '@iconscout/unicons/svg/line/whatsapp.svg';
import LogoMobile from '../../assets/icons/krossmet_logo_mobile.svg';
import Logo from '../../assets/icons/krossmet_logo.svg';

import { HeaderProps, NavItem } from './Header.types';

import * as s from './Header.module.css';

const Header: React.FunctionComponent<HeaderProps> = ({ navs, className }) => {
	const [navActive, setNavActive] = React.useState(false);
	const { breakpoint } = useBreakpoint(breakpoints, "desktopSmall");
	const isMobile = breakpoint === "mobile";

	const handleTogglerClick = () => {
		setNavActive((prev) => !prev);
	};

	const renderNavItem = (item: NavItem) => (
		<li key={item.href} className={s.navItem}>
			<a
				className={classnames(s.navLink, { [s.navLinkActive]: item.isActive })}
				href={item.href}
				onClick={handleTogglerClick}
			>
					{item.title}
			</a>
		</li>
	);

	const renderNavs = (items: NavItem[]) => (
		<nav className={s.nav}>
			<Button className={s.navButton} icon={BarsIcon} label="Открыть меню" onClick={handleTogglerClick} />
			<div className={classnames(s.listWrapper, { [s.listWrapperActive]: isMobile && navActive })}>
				{isMobile && 
					<div className={s.listHeader}>
						{renderPhones()}
						<Button className={s.closeButton} icon={CloseIcon} label="Закрыть меню" onClick={handleTogglerClick} />
					</div>
				}
				<ul className={s.list}>
					{items.map(renderNavItem)}
				</ul>
			</div>
		</nav>
	);

	const renderPhones = () => {
		return (
			<div>
				<Text className={classnames(s.contactsDetail, s.contactsDetailPhone)} weight="bold">
					<a href="tel:+79000000000">
						<PhoneIcon className={s.contactsIcon} />
						+7 (495) 654 23 34
					</a>
				</Text>
				<Text className={classnames(s.contactsDetail, s.contactsDetailPhone)} weight="bold">
					<a target="_blank" href={`https://api.whatsapp.com/send?phone=+74952970005&text=${encodeURIComponent("Здравствуйте! У меня вопрос по поводу продукции RBS Krossmet")}`}>
						<WhatsAppIcon className={s.contactsIcon} />
						+7 (495) 297 00 05
					</a>
				</Text>
			</div>
		);
	};

	return (
		<header className={classnames(s.root, className)}>
			<Container className={s.inner}>
				<a className={s.logo} href="/">
					<Logo className={s.svgLogoDesktop} />
					<LogoMobile className={s.svgLogoMobile} />
				</a>
				<div className={s.contacts}>
					{renderPhones()}
					<Text className={s.contactsDetail} weight="medium">
						<a href="mailto:info@rbs.ru">
							<EnvelopeIcon className={s.contactsIcon} />
							info@rbs.ru
						</a>
					</Text>
				</div>
				{Array.isArray(navs) && navs.length && renderNavs(navs)}
			</Container>
		</header>
	);
};

export default Header;
