import * as React from 'react';
import classnames from 'classnames';

import { TextProps } from './Text.types';

import * as s from './Text.module.css';

const Text: React.FunctionComponent<TextProps> = (props) => {
	const mods = {
		[s.small]: props.size === "small",
		[s.big]: props.size === "big",
		[s.huge]: props.size === "huge",
		[s.medium]: props.weight === "medium",
		[s.bold]: props.weight === "bold",
		[s.extrabold]: props.weight === "extrabold",
		[s.black]: props.weight === "black",
		[s.contrast]: props.contrast,
	};

	return (
		<p className={classnames(s.root, props.className, mods)}>{props.children}</p>
	);
};

export default Text;