import * as React from 'react';
import classnames from 'classnames';

import SpinnerIcon from '@iconscout/unicons/svg/line/spinner.svg';

import { ButtonProps } from './Button.types';

import * as s from './Button.module.css';

const Button: React.FunctionComponent<ButtonProps> = (props) => {
	const Icon = props.icon;
	const mods = {
		[s.primary]: props.primary,
		[s.secondary]: props.secondary,
		[s.small]: props.size === "small",
		[s.big]: props.size === "big",
		[s.wide]: props.wide,
		[s.pulse]: props.pulse,
		[s.iconOnly]: props.icon && !props.children,
		[s.loading]: props.loading,
		[s.light]: props.theme === "light"
	};

	const renderButtonContent = () => {
		return (
			<>
				{props.loading && (
					<span className={s.spinnerWrapper}>
						<SpinnerIcon className={s.spinner} />
					</span>
				)}
				{Icon && <Icon className={s.icon} />}
				<span className={s.content}>{props.children}</span>
			</>
		);
	};

	const rootClassNames = classnames(s.root, props.className, mods);

	if (props.href) {
		return (
			<a
				href={props.href}
				target={props.target}
				className={rootClassNames}
				aria-label={props.label}
			>
				{renderButtonContent()}
			</a>
		);
	}

	return (
		<button
			disabled={props.disabled}
			onClick={props.onClick}
			className={rootClassNames}
			aria-label={props.label}
		>
			{renderButtonContent()}
		</button>
	);
};

export default Button;
