import * as React from 'react';
import classnames from 'classnames';

import Text from '../Text';
import Title from '../Title';
import Button from '../Button';

import { ProductCardProps, ProductRecord } from './ProductCard.types';

import * as s from './ProductCard.module.css';

const ProductCard:React.FunctionComponent<ProductCardProps> = props => {
	const renderRecord = (record: ProductRecord, index: number) => {
		const { title, text, number } = record;

		return (
			<li key={index}>
				<Text className={s.record}>
					{title && <strong className={s.recordTitle}>{title}</strong>}
					{text}
					{number && <span className={s.recordNumber}>{number}</span>}
				</Text>
			</li>
		);
	};

	return (
		<div className={classnames(s.root, props.className, { [s.selected]: props.selected })}>
			<div className={s.image}>{props.image}</div>
			<Title className={s.title} tagName="span">{props.title}</Title>
			{props.oldPrice && <Text className={s.oldPrice}>{props.oldPrice}</Text>}
			<Text className={s.price}>{props.price}</Text>
			<ul className={s.records}>
				{props.records.map(renderRecord)}
			</ul>
			<Button
				onClick={props.onClick}
				className={s.button}
				wide
				primary={!props.selected}
				secondary={props.selected}
			>
				Купить
			</Button>
		</div>
	);
};

export default ProductCard;