import * as React from 'react';
import classnames from 'classnames';

import { IMaskInput } from 'react-imask';
import Text from '../Text';

import { FormFieldProps } from './FormField.types';

import * as s from './FormField.module.css';

const FormField: React.FunctionComponent<FormFieldProps> = (props) => {
	const fieldProps = {
		className: classnames(s.input, props.className, {
			[s.textarea]: props.type === 'textarea'
		}),
		name: props.name,
		type: props.type || 'text',
		placeholder: props.placeholder,
		autoComplete: props.autoComplete,
		onChange: props.onChange,
		value: props.value,
		disabled: props.disabled,
	};

	const rootClassName = classnames(s.root, props.rootClassName, {
		[s.error]: props.error,
		[s.selectWrapper]: props.type === "select"
	});

	const renderInputByType = function () {
		switch (props.type) {
			case 'textarea':
				return <textarea {...fieldProps}>{props.children}</textarea>;
			case 'select':
				return (
					<select {...fieldProps}>
						{props.options && props.options.map((option) => (
							<option key={option.value} selected={props.value === option.value} value={option.value}>{option.name}</option>
						))}
					</select>
				);
			case 'tel':
				return (
					<IMaskInput
						{...fieldProps}
						mask="+{7} (000) 000-00-00"
					/>
				);
			default:
				return <input {...fieldProps} />;
		}
	};

	return (
		<div className={rootClassName}>
			{renderInputByType()}
			{props.error && <Text className={s.errorText}>{props.error}</Text>}
		</div>
	);
};

export default FormField;