import * as React from 'react';
import classnames from 'classnames';

import Text from '../Text';
import CheckIcon from '@iconscout/unicons/svg/line/check-circle.svg';
import ErrorIcon from '@iconscout/unicons/svg/line/exclamation-triangle.svg';

import { MessageProps } from './Message.types';

import * as s from './Message.module.css';

const Message:React.FunctionComponent<MessageProps> = (props) => {
	return (
		<Text className={classnames(s.root, props.className, { [s.error]: props.type === "error" })}>
			{
				props.type === "error"
					? <ErrorIcon className={s.messageIcon} />
					: <CheckIcon className={s.messageIcon} />
			}
			{props.children}
		</Text>
	);
};

export default Message;