import * as React from "react";
import classnames from "classnames";
import { useFormik } from 'formik';
import validator from 'validator';
import axios from 'axios';

import PhoneIcon from '@iconscout/unicons/svg/line/phone.svg';
import EnvelopeIcon from '@iconscout/unicons/svg/line/envelope.svg';
import WhatsAppIcon from '@iconscout/unicons/svg/line/whatsapp.svg';

import Container from '../Container';
import Text from '../Text';
import Button from "../Button";
import FormField from "../FormField";
import Message from "../Message";

import { FooterProps } from './Footer.types';
import { ServerResponse } from '../OrderForm/OrderForm.types';

import * as s from './Footer.module.css';

const Footer: React.FunctionComponent<FooterProps> = (props) => {
	const [message, setMessage] = React.useState<string>();
	const [error, setError] = React.useState<string>();
	const [processing, setProcessing] = React.useState(false);
	const { handleSubmit, handleChange, values, errors } = useFormik({
		initialValues: {
			email: '',
		},
		onSubmit: async (values) => {
			try {
				setMessage("");
				setProcessing(true);
				const response: ServerResponse = await axios.post("https://formspree.io/f/mknkpopy", values);

				if (response.data.ok) {
					setMessage('Спасибо. Ожидайте, скоро мы с вами свяжемся.');
				} else if (response.data.error) {
					setError("Произошла ошибка. Попробуйте позже.");
				}
			} catch (error) {
				setError("Произошла ошибка. Попробуйте позже.");
			} finally {
				setProcessing(false);
			}
		},
		validateOnChange: false,
		validateOnBlur: false,
		validate: (values) => {
			const errors: Partial<Record<keyof typeof values, string>> = {};

			if (values.email === "") {
				errors.email = "Заполните это поле";
			} else if (!validator.isEmail(values.email)) {
				errors.email = "Проверьте правильность введенного Email";
			}

			return errors;
		}
	});

	return (
		<footer className={classnames(s.root, props.className)}>
			<Container className={s.container}>
				<ul className={s.contacts}>
					<li className={s.contactsItem}>
						<PhoneIcon className={s.contactsIcon} />
						<Text className={s.contactsTitle} weight="bold" size="big">Телефон</Text>
						<Text className={s.contactsText} size="big">
							<a href="tel:+79000000000">8 495 434 34 95</a>
						</Text>
					</li>
					<li className={s.contactsItem}>
						<EnvelopeIcon className={s.contactsIcon} />
						<Text className={s.contactsTitle} weight="bold" size="big">Email</Text>
						<Text className={s.contactsText} size="big">
							<a href="mailto:info@rbs.ru">info@rbs.ru</a>
						</Text>
					</li>
					<li className={s.contactsItem}>
						<WhatsAppIcon className={s.contactsIcon} />
						<Text className={s.contactsTitle} weight="bold" size="big">WhatsApp</Text>
						<Text className={s.contactsText} size="big">
							<a href="tel:+79000000000">8 495 434 34 95</a>
						</Text>
					</li>
				</ul>
				<div>
					<Text className={s.formText} weight="bold">Остались вопросы? Оставьте свой Email и мы будем рады проконсультировать вас.</Text>
					{
						message ? (
							<Message type="success">{message}</Message>
						) : (
							<>
								{error && <Message type="error">{error}</Message>}
								<form className={s.form} onSubmit={handleSubmit}>
									<FormField
										error={errors.email}
										rootClassName={s.formInputWrapper}
										className={s.formInput}
										placeholder="Ваш email"
										name="email"
										value={values.email}
										disabled={processing}
										onChange={handleChange as any}
									/>
									<Button
										className={s.formButton}
										disabled={processing}
										loading={processing}
									>
										Напишите мне
									</Button>
								</form>
							</>
						)
					}
				</div>
			</Container>
		</footer>
	);
};

export default Footer;
