import * as React from 'react';
import classnames from 'classnames';

import Title from '../Title';
import Text from '../Text';

import { PurposeProps } from './Purpose.types';

import * as s from './Purpose.module.css';

const Purpose:React.FunctionComponent<PurposeProps> = (props) => {
	const IconComponent = props.icon;

	return (
		<div className={classnames(s.root, props.className, { [s.horizontal]: props.horizontal })}>
			<div className={s.circle}>
				<IconComponent className={s.icon} />
			</div>
			<Title className={s.title} level={3}>{props.title}</Title>
			{props.text && <Text className={s.text} weight="medium">{props.text}</Text>}
		</div>
	);
};

export default Purpose;