import * as React from 'react';
import classnames from 'classnames';
import { useFormik } from 'formik';
import validator from 'validator';
import axios from 'axios';

import FormField from '../FormField';
import Text from '../Text';
import Button from '../Button';
import Message from '../Message';

import { OrderFormProps, Field, ServerResponse } from './OrderForm.types';

import * as s from './OrderForm.module.css';

const PHONE_REGEX = /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/;

const OrderForm:React.FunctionComponent<OrderFormProps> = ({ submitProps, theme, className, action, withPositions, initialValues }) => {
	const [message, setMessage] = React.useState<string>();
	const [error, setError] = React.useState<string>();
	const [processing, setProcessing] = React.useState(false);

	const { handleSubmit, handleChange, values, errors } = useFormik({
		initialValues: {
			name: '',
			email: '',
			phone: '',
			...(withPositions ? { position: '' } : {}),
			...initialValues
		},
		onSubmit: async (values) => {
			try {
				setMessage("");
				setProcessing(true);
				const response: ServerResponse = await axios.post(action, values);

				if (response.data.ok) {
					setMessage('Ваша заявка отправлена. Ожидайте, мы с вами свяжемся.');
				} else if (response.data.error) {
					setError('Произошла ошибка. Попробуйте позже.');
				}
			} catch (error) {
				setError('Произошла ошибка. Попробуйте позже.');
			} finally {
				setProcessing(false);
			}
		},
		validateOnChange: false,
		validateOnBlur: false,
		validate: (values) => {
			const errors: Partial<Record<keyof typeof values, string>> = {};

			if (!values.position && withPositions) {
				errors.position = "Выберите товар";
			}

			if (values.email === "" && values.phone === "") {
				errors.email = "Заполните это поле";
			}

			if (values.name === "") {
				errors.name = "Заполните это поле";
			} else if (!validator.isAlpha(values.name, "ru-RU")) {
				errors.name = "Введите имя на русском языке";
			}

			if (!validator.isEmail(values.email) && values.email !== "") {
				errors.email = "Проверьте правильность введенного Email";
			}

			if (!PHONE_REGEX.test(values.phone) && values.phone !== "") {
				errors.phone = "Введите номер телефона в формате: +7 (000) 000-00-00";
			}

			return errors;
		}
	});

	const fields: Field[] = [
		{
			name: "name",
			label: "Ваше имя",
			type: "text",
			required: true,
		},
		{
			name: "email",
			label: "Ваш Email",
			type: "email",
			required: true,
		},
		{
			name: "phone",
			label: "Ваш номер телефона",
			type: "tel",
			required: true,
		}
	];

	if (withPositions) {
		fields.unshift({
			name: "position",
			type: "select",
			label: "Название товара",
			required: true,
			options: [
				{
					name: "RBS 101",
					value: "RBS 101"
				},
				{
					name: "RBS 101 Profi",
					value: "RBS 101 Profi"
				},
				{
					name: "RBS 102 Profi",
					value: "RBS 102 Profi"
				}
			]
		});
	}

	const renderField = (field: Field) => (
		<label className={s.formField} key={field.name}>
			<Text className={s.formLabel}>
				{field.label}
				{field.required && <sup>*</sup>}
			</Text>
			<FormField
				className={s.formInput}
				type={field.type}
				name={field.name}
				error={errors[field.name]}
				value={values[field.name]}
				disabled={processing}
				onChange={handleChange as any}
				options={field.options}
			/>
		</label>
	);

	const renderFormContent = () => {
		return (
			<>
				{error && <Message className={s.message} type="error">{error}</Message>}
				{fields.map(renderField)}
				<Button
					loading={processing}
					disabled={processing}
					className={classnames(s.formSubmit, submitProps?.className)}
					{...submitProps}
				/>
			</>
		);
	};

	const formClassNames = classnames(s.root, className, { [s.dark]: theme === "dark" });
   
	return (
		<form onSubmit={handleSubmit} className={formClassNames}>
			{
				message ? (
					<Message className={s.message} type="success">{message}</Message>
				) : renderFormContent()
			}
		</form>
	);
};

export default OrderForm;